
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IWeightClassForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import WeightClassForm from '@/views/system/localization/weight-class/forms/WeightClassForm.vue'

export interface IData {
  model: IWeightClassForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [loadForm, loadStatus, loadError] = useFetching((id: number) => {
      return store.dispatch('weightClass/loadForm', id)
    }) as ResponseFetching<Promise<IWeightClassForm>>

    const [saveForm, saveStatus, saveError] = useFetching((id: number, dataForm: IWeightClassForm) => {
      return store.dispatch('weightClass/updateForm', { id, dataForm })
    }) as ResponseFetching<Promise<IWeightClassForm>>

    const form = ref()

    return {
      loadForm,
      loadStatus,
      loadError,
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    WeightClassForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending || this.loadStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: null
    }
  },

  async mounted () {
    this.model = await this.loadForm(this.$route.params.id)
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'weight-class' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IWeightClassForm = await this.saveForm(this.$route.params.id, this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }
      }
    }
  }
})
